.pika-button {
    color: #2b2b2b;
    background-color: #fff;
}

.pika-title {
    color: #2b2b2b;
    font-size: 24px;
    font-weight: 700;
}

.pika-single {
    font-family: "Roboto", sans-serif;
}

.pika-button:hover, .pika-row.pick-whole-week:hover .pika-button {
    background: map_get($blue, default);
}

.is-selected .pika-button, .has-event .pika-button {
    background: map_get($blue, default);
    box-shadow: none;
}

.pika-table abbr {
    border-bottom: none;
    text-decoration: none;
    cursor: pointer;
}

.is-today .pika-button {
    color: #2b2b2b;
    font-weight: bold;
    border-radius: 3px;
    background-color: #f3f3f3;
}

.is-disabled .pika-button {
    pointer-events: none;
    cursor: default;
    color: #999;
    background-color: #fff;
    opacity: .3;
}

.is-today .pika-button:hover {
    color: #fff;
    background-color: map_get($blue, default);
}