@import "vars";
@import 'mixins';
@import 'blocks/full-width-slider';
@import 'parts/drug-and-drop';
@import 'parts/datepicker';

.vip-gift__information {
    .page-title {
        text-align: center;
        padding-left: 0;
    }
    .information {
        p {
            text-indent: 30px;
        }
    }
    padding: 30px 245px 0;
    background-color: #fff;
    .product-horizontal-line {
        margin: 25px 0 15px 0;
        float: none;
    }
}


.btn-center-block {
    text-align: center;
    width: 100%;
    .more-info-link {
        color: map_get($blue, default);
        text-decoration: underline;
        transition: color .3s;
        display: inline-block;
        font-size: 14px;
        &:hover {
            color: map_get($blue, hover);  
        }
        &:active {
            color: map_get($blue, active);  
        }
    }
}

.page-title {
    margin: 35px 0 35px 0;
}


.custom-form {
    padding-bottom: 30px;
    form {
        margin-top: 35px;
    }
    form .blue-btn {
        width: auto;
        padding-left: 35px;
        padding-right: 35px;
        display: flex;
        justify-content: center;
        margin: 0 auto;
    }
    &__contact-information {
        margin-top: 15px;
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        .custom-form__col {
            width: 320px;
        }
    }
    &__delivery-method{
        margin-top: 15px;
        margin-bottom: 30px;
        .custom-radio-button {
            display: block;
            margin-bottom: 5px;
            label span {
                font-weight: 400;
            }
        }
    }
    &__select-products {
        margin-top: 10px;
        padding-bottom: 40px;
        .filter__checkbox + label {
            color: map_get($gray, default);
            font-size: 14px;
            font-weight: 700;
            line-height: 16px;
            cursor: pointer;
            display: inline-block;
        }
        .filter__checkbox-row {
            margin-bottom: 5px;
        }
    }
    .form-subtitle {
        color: map_get($gray-light, default);;
        font-size: 12px;
    }
    &__buttons {
        margin-top: 10px;
        margin-bottom: 30px;
        display: flex;
        justify-content: space-between;
        .custom-form__col {
            display: inline-block;
            vertical-align: top;
            text-align: center;
            &--upload {
                width: 280px;
                .form-subtitle.form-subtitle {
                    text-align: right;
                    padding-right: 20px;
                }
            }
            &--center {
                margin-left: auto;
            }
            .form-subtitle {
                margin-bottom: 15px;
                text-align: center;
            }
            .open-prod-controls__title {
                margin-left: 15px;
            }
        }
    }
    &__more-info {
        margin-top: 10px;
        padding-bottom: 40px;
        .form-notice {
            font-size: 14px;
        }
    }
    .custom-form__wrap {
        display: inline-block;
        width: 100%;
        margin-bottom: 20px;
        position: relative;
        // .custom-form__input {
        //     width: 95%;
        // }
    }
}

// for drag and drop
.order-form {
    .files-container {
        position: relative;
        .warning {
            left: 65px;
        }
        .drop-wrap {
            margin-top: 0;
            #drop {
                left: 65px;
            }
            .file-name-wrap {
                display: block;
            }
            &.hover {
                .drop-hint {
                    left: 65px;
                }
            }
        }
    }
    .imgs-for-upload  {
        margin-bottom: 20px;
        ul li p {
            width: 92%;
        }
    }
}


