.files-container {
    .warning {
        color: #ec3276;
        font-size: 14px;
        margin: 17px 0 0 0;
        white-space: nowrap;
        display: none;
        position: absolute;
    }
    .drop-wrap {
        position: relative;
        width: 100%;
        height: 30px;
        text-align: center;
        margin-top: 25px;
        .drop-hint {
            display: none;
            pointer-events: none;
        }
        #drop {
            // height: 200px;
            // width: 500px;
            // margin-left: -96px;
            // padding-left: 96px;
            // margin-top: -100px;
            // padding-top: 100px;
            // line-height: 200px;
            // height: 120px;
            height: 160px;
            width: 100%;
            left: 0;
            font-size: larger;
            position: absolute;
            border: 1px solid transparent;
            transition: background 300ms;
        }
        &.hover {
            .drop-hint {
                display: block;
                text-align: center;
                width: 100%;
                // color: #777;
                color: #a6a6a6;
                font-size: 18px;
                font-weight: 400;
                z-index: 20;
                position: absolute;
                top: 70px;
            }
            #drop{
                border: 1px dashed #b5b5b5;
                background: #eee;
                z-index: 3;
            }
            .file-name-wrap {
                visibility: hidden;
            }
        }
        .drop-title {
            color: #a6a6a6;
            font-size: 12px;
            font-weight: 400;
        }
        .file-name-wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            z-index: 2;
        }
        .file-name {
            position: relative;
        }
        input {
            display:none;
        }
    }
}

.imgs-for-upload {
    ul {
        margin-top: 40px;
        li {
            background-color: #f6f6f6;
            height: 38px;
            padding: 11px 8px;
            position: relative;
            margin-bottom: 10px;
            border: 1px solid #eee;
            cursor: default;

            -webkit-transition: background .3s;
            -o-transition: background .3s;
            transition: background .3s;
            // &:last-child {
            //     margin-bottom: 0;
            // }
            &:hover {
                background: #ececec;
                .del {
                    position: absolute;
                    width: 12px;
                    height: 12px;
                    // top: 8px;
                    top: 50%;
                    margin-top: -6px;
                    right: 8px;
                    display: inline-block;
                    cursor: pointer;
                    background-image: url('../img/icon/сross_icon.svg');
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center;
                    opacity: 0.8;
                    &:hover {
                        opacity: 1;
                    }
                }
            }

            input {
                display: none;
            }
            p {
                padding: 0;
                margin: 0;
                color: #000;
                font-weight: 400;
                float: left;
                text-align: left;
                padding-left: 0;
                line-height: 14px;
                margin-left: 35px;
                width: 87%;
                span {
                    width: 200px;
                    font-size: 13px;
                    line-height: 16x;
                    color: #2b2b2b;
                    display: inline-block;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .del {
                    display: none;
                }
            }
            i {
                font-weight: normal;
                font-style: normal;
                color: #2b2b2b;
                display: block;
                width: 95px;
                color: #636363;
                font-size: 13px;
                float: right;
                position: relative;
                padding: 2px 5px 0 0;
                display: block;
                &:after {
                    content: '';
                    text-align: right;
                    width: 19px;
                    height: 19px;
                    position: absolute;
                    top: -2px;
                    right: 10px;
                    background: url('../img/icon/done.svg');
                    background-size: 19px 19px;
                    background-repeat: no-repeat;
                    background-position: center center;
                }
            }
            canvas {
                top: 8px;
                left: 12px;
                position: absolute;
            }
            &.working {
                span {
                    height: 16px;
                    background-position: 0 -12px;
                }
            }
            &.error {
                p {
                    color: red;
                }
            }
        }
    }
}